import React, {Component} from 'react';
import axios from 'axios';
import './filter.css';

const url = "https://zomatojulyapi.herokuapp.com/filter"


class CuisineFilter extends Component{
    filterCuisine = (event) => {
        let mealId = sessionStorage.getItem('mealId');
        let cuisineId = event.target.value;
        let filterUrl;
        if(cuisineId == ""){
            filterUrl = `${url}/${mealId}`
        }else{
            filterUrl = `${url}/${mealId}?cuisine=${cuisineId}`
        }
        axios.get(filterUrl)
        .then((res) => {this.props.restPerCusinie(res.data)})
    }
    render(){
        return(
            <div className="cuisineFilter">
           <center> <h2 className="fontsize1">Filter</h2></center>
            <h3 className="fontsize">Cuisine Filter</h3>
            <div onChange={this.filterCuisine}>
                <label className="radio">
                    <input type="radio"name="cuisine" value="" className="radio"/>All
                </label>
                <label className="radio">
                    <input type="radio"name="cuisine" value="1" className="radio"/>North Indian
                </label>
                <label className="radio">
                    <input type="radio"name="cuisine" value="2" className="radio"/>Lunch
                </label>
                <label className="radio">
                    <input type="radio"name="cuisine" value="3" className="radio"/>Chenies
                </label>
                <label className="radio">
                    <input type="radio"name="cuisine" value="4" className="radio"/>Fast Food
                </label>
                <label className="radio">
                    <input type="radio"name="cuisine" value="5" className="radio"/>Street Food
                </label> <label className="radio">
                    <input type="radio"name="cuisine" value="" className="radio"/>North Indian
                </label>
            </div>
            </div>
        )
    }
}

export default CuisineFilter
